import React from 'react'

export default function LinedHeading(props) {
  return (
    <div className='my-container flex flex-row justify-center xl:justify-between items-center'>
    <div className='hidden xl:block w-2/6 h-[1px] bg-white'></div>
    <div className='sub-heading-med-m my-5'>
      {props.children}
    </div>
    <div className='hidden xl:block w-2/6 h-[1px] bg-white'></div>
    </div>
  )
}
