import React from "react";
import Carousel from "better-react-carousel";
import rightArrow from "../../assets/icons/right-arrow.svg";
import leftArrow from "../../assets/icons/left-arrow.svg";

import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

export default function ImgSlider(props) {
  const blocks = props.blocks;

  let gridCols = 5;
  if(blocks.length < 5){
    gridCols = blocks.length
  }

  const blocksCode = blocks.map((block) => (
    <Carousel.Item>{block}</Carousel.Item>
  ));

  return (
    <div className={`my-container grid gap-y-6 grid-cols-2 md:grid-cols-3 lg:grid-cols-4 xl:grid-cols-${gridCols}`}>
      {blocksCode}
    </div>
  );
}
